import {
  Bar,
  BarChart,
  Cell,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from "recharts";
import { getProfessionColour } from "../helpers/staticData";

export const TopPlayerStatChart = ({ data }) => {
  return (
    <>
      <ResponsiveContainer>
        <BarChart
          data={data}
          layout="vertical"
          barCategoryGap={2}
          margin={{
            top: 2,
            right: 10,
            left: -50,
            bottom: 5,
          }}
        >
          <YAxis
            type="category"
            dataKey="name"
            tick={false}
          />
          <XAxis
            type="number"
            dataKey="value"
            domain={[0, "dataMax"]}
          />
          <Tooltip
            cursor={false}
            content={renderCusomizedToolTip}
          />
          <Bar
            dataKey="value"
            label={(e) =>
              renderCustomizedLabel(
                e,
                data.find((x) => x.name === e.name)
              )
            }
            activeBar={{ filter: `brightness(1.5)`, strokeWidth: 2 }}
          >
            {data.map((bar, i) => {
              return (
                <Cell
                  key={`cell-${i}`}
                  fill={getProfessionColour(bar.profession)}
                />
              );
            })}
          </Bar>
        </BarChart>
      </ResponsiveContainer>
    </>
  );
};

const renderCustomizedLabel = (props, data) => {
  const { x, y, value, name, height } = props;

  return (
    <text
      y={y + height / 2}
      x={x + 2}
      dominantBaseline="middle"
    >
      {String(data.num_fights_top).padStart(2, "0")}|
      {String(data.num_fights_present).padStart(2, "0")} {name}:{" "}
      {value.toLocaleString()}
    </text>
  );
};

const renderCusomizedToolTip = ({ active, payload, label }) => {
  if (active && payload && payload.length) {
    return (
      <div className="customTooltip">
        <p
          className="tooltipName"
          style={{
            "background-color": getProfessionColour(
              payload[0].payload.profession
            ),
          }}
        >{`${label}`}</p>
        <p className="tooltipProfession">{`${payload[0].payload.profession}`}</p>
        <p className="tooltipValue">{`${payload[0].value.toLocaleString()}`}</p>
      </div>
    );
  }

  return null;
};

export default TopPlayerStatChart;
